.offcanvas.offcanvas-start.article-menu-canvas {
  padding: 64px 24px 24px 24px;
  overflow: auto;
  height: 100%;
  --bs-offcanvas-width: 428px !important;
}
.offcanvas.offcanvas-start.article-menu-canvas .close-btn-wrapper {
  --bs-offcanvas-width: 428px !important;
  height: 48px;
  width: 100%;
  max-width: 410px;
  left: 0;
  z-index: 2;
}
@media (min-width: 1200px) {
  .offcanvas.offcanvas-start.article-menu-canvas {
    padding: 0px 35px 24px 0px;
    overflow: unset;
    position: relative;
    transform: none;
    visibility: visible;
    display: block;
    box-shadow: none;
    transition: none;
    padding: 48px 35px 24px 36px;
  }
  .offcanvas.offcanvas-start.article-menu-canvas + .offcanvas-backdrop.fade {
    display: none;
  }
  .offcanvas.offcanvas-start.article-menu-canvas .btn-close.text-reset {
    display: none;
  }
}
.offcanvas.offcanvas-start.article-menu-canvas .btn-close {
