  height: 13.5px;
  background-size: 13.5px 13.5px;
  z-index: 3;
}
.offcanvas.offcanvas-start.article-menu-canvas .btn-close:focus {
  box-shadow: none;
}
.offcanvas.offcanvas-start.article-menu-canvas h2 {
